//Import des fonctionnalitées des composants react
import React from "react";

//Import de Link from
import { Link } from "react-router-dom";

//Import du css du composant
import css from "../testComponent/NavbarRes.module.css";

//Import logo
import logo from "../../assets/img/Instagram-Logo.png";

//Import icons React
import { FaShoppingCart } from "react-icons/fa";
import { VscAccount } from "react-icons/vsc";

class NavbarRes extends React.Component {
  //State

  //Render
  render() {
    return (
      <>
        <nav className={css.navbar}>
          <div className={css.logo}>
            <Link to="" className={css.navlinks}>
              <img className={css.nav_logo} src={logo} alt="Instagram's Logo" />
            </Link>
          </div>
          <div className={css.nav_links}>
            <div className={css.leftNav}>
              <ul>
                <li>
                  <Link to="" className={css.navlinks}>
                    Technologie
                  </Link>
                </li>
                <li>
                  <Link to="" className={css.navlinks}>
                    Tuto
                  </Link>
                </li>
              </ul>
            </div>
            <div className={css.rightNav}>
              <ul>
                <li>
                  <Link to="" className={css.navlinks}>
                    <VscAccount />
                  </Link>
                </li>
                <li>
                  <Link to="" className={css.navlinks}>
                    <FaShoppingCart />
                  </Link>
                </li>
              </ul>
            </div>
          </div>
          <div className={css.menuToggler}>
            <button className={css.menu}>Menu</button>
          </div>
        </nav>
      </>
    );
  }
}

export default NavbarRes;
